<template>
    <div>
        <table class="table-hover table-sm">
            <tr>
                <th>Session Id</th>
                <th>Goal Id</th>
                <th>User Id</th>
                <th>Registed Date</th>
                <th>Modificated Date</th>
                <th>Session Name</th>
                <th>Laps</th>
                <th>rushTime</th>
                <th>restTime</th>
                <th>대표 이미지</th>
            </tr>
            <tr v-for="(list, i) in state.lists" :key="(list, i)">
                <td>{{ list.sessid }}</td>
                <td>{{ list.goalid }}</td>
                <td>{{ list.uid }}</td>
                <td>{{ list.regDate }}</td>
                <td>{{ list.modDate }}</td>
                <td>{{ list.name }}</td>
                <td>{{ list.laps }}</td>
                <td>{{ list.rushTime }}</td>
                <td>{{ list.restTime }}</td>
                <td><img v-bind:src="state.img[i]" alt="대표이미지"></td>
            </tr>
        </table>
    </div>
</template>
    
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios';
import dayjs from 'dayjs'
export default {
    setup() {
        const state = reactive({
            lists: '',
            img: [],
            email: sessionStorage.getItem('email'),
            token: sessionStorage.getItem('TOKEN'),
        });
        const formatDate = (dateString) => {
            const date = dayjs(dateString);
            return date.format('YY년 MM월 D일');
        }
        const url = "http://localhost:9090/api/sessionList"
        const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token,
        }
        const body = { email: state.email }
        axios.post(url, body, { headers }).then(function (res) {
            if (res.data != null) {

                state.lists = res.data
                showImage(res.data);
            } else {
                alert('too bad. check something')
            }
        })
        const showImage = async (arr) => {
            const url = `http://localhost:9090/display`
            let str = "";
            if (arr.length == 0) {
                str = ``
            } else {
                for (let i = 0; i < arr.length; i++) {
                    str = `${url}?fileName=${arr[i].fileDTOList[0].thumbnailURL}`;
                    state.img[i] = str;
                }
            }
        }
        return { state, formatDate }
    }
}
</script>
    
<style scoped>
table {

    width: 1000px;

    border-top: 1px solid #444444;
    border-collapse: collapse;
    margin: 0 auto;
}

th {
    border-bottom: 1px solid #444444;
    padding: 10px;
    text-align: center;
    background: blue;
    color: white;
}

td {
    border-bottom: 1px solid #aaa;
    padding: 8px;
    text-align: center;
}
</style>